'use client';
import { SessionProvider } from 'next-auth/react';

export function Providers({ children }: { children: React.ReactNode }) {
  // Перевіряємо, чи знаходимось на клієнтській стороні
  if (typeof window === 'undefined') {
    return <>{children}</>;
  }
  
  return (
    <SessionProvider>
      {children}
    </SessionProvider>
  );
}